.dse-input-container {
  display: flex;
  position: relative;
  align-items: center;
  padding: 0.75rem;
  border: 1px solid var(--dse-form-border-color, #f1f2f3);
  background-color: var(--dse-form-bg-color, #fff);
  border-radius: 0.25rem;
  box-sizing: border-box;
  gap: 0.75rem;
  cursor: text;
}

.dse-input-container:focus-within {
  box-shadow: 0 4px 11px rgba(157, 157, 157, 0.21);
  box-sizing: border-box;
}

.dse-input-container input {
  border: none;
  outline: none;
  box-sizing: border-box;
  font-family: var(--dse-body-font-family, "Playfair Display", serif);
  font-size: var(--dse-body-font-size, 0.875rem);
  padding: 0;
  flex-grow: 1;
}
